.homeStart {
    /*
    background: #1fc8db linear-gradient(141deg, #8ef6e4 32.5%, #9896f1 60%, #d59bf6 82.5%, #edb1f1 100%);
    background: linear-gradient(-141deg, #d7fbe8 0%, #1fc8db 51%, #2cb5e8 75%) fixed;
    */
    background: var(--colorfirst) url('../../images/background-tile(1).png') fixed;
    box-sizing: border-box;
    color: var(--colorfifth);
    height: 100vh;
    padding-top: 30vh;
    position: relative;
}

.homeStart > h1 {
    font-size: 70px;
    margin-left: 20px;
    margin-right: 20px;
    margin-top: 0;
    text-align: center;
}

.homeStart > div > ul {
    list-style-type: none;
    padding: 0;
    text-align: center;
}

.homeStart > div > ul > li > h2 {
    font-size: 30px;
    margin: 0;
}

@media (max-width: 999px) {

    .homeStart > h1 {
        font-size: 40px;
        margin-top: 0;
        text-align: center;
    }

    .homeStart > div > ul > li > h2 {
        font-size: 25px;
        margin: 0;
    }
}